<template>
<v-container>
  <v-layout row wrap>
		<v-spacer></v-spacer>
    <v-flex xs6 sm8>
      <v-form>
        <v-text-field label="Summoner Name" v-model="name" required></v-text-field>
				<br>{{ gameIds }}
        <br>League: {{ tier }}
        <br>Division: {{ rank }}
        <br>League points: {{ lp }}
				<br>Win Rate: {{ wr }}
      </v-form>
    </v-flex>
    <v-flex xs3 sm2 lg1>
      <v-select v-bind:items="items" v-model="server" label="Server" single-line bottom></v-select>
    </v-flex>
		<v-flex xs3 sm2 lg1>
			<v-btn raised large class="primary" dark @click.native="getLeaguePos(), getCurrentGame()">GO!</v-btn>
		</v-flex>
		<v-spacer></v-spacer>
  </v-layout>
</v-container>
</template>
<script>
var axios = require('axios')
export default {
  data () {
    return {
      name: null,
      server: null,
      summonerId: '',
      lp: '',
      rank: '',
      wr: '',
      tier: '',
      gameIds: [],
      summoners: [],
      items: [{
        text: 'EUW',
        value: 'euw1'
      },
      {
        text: 'NA',
        value: 'na1'
      },
      {
        text: 'RU',
        value: 'ru'
      },
      {
        text: 'BR',
        value: 'br1'
      },
      {
        text: 'KR',
        value: 'kr1'
      },
      {
        text: 'OC',
        value: 'oc1'
      },
      {
        text: 'JP',
        value: 'jp1'
      },
      {
        text: 'EUN',
        value: 'eun1'
      },
      {
        text: 'TR',
        value: 'tr1'
      },
      {
        text: 'LA1',
        value: 'la1'
      },
      {
        text: 'LA2',
        value: 'la2'
      }
      ]
    }
  },
  methods: {
    getLeaguePos: function () {
      this.lp = 'Loading...'
      this.rank = 'Loading...'
      this.tier = 'Loading...'
      this.wr = 'Loading...'
      var self = this
      axios.post('../php/testing.php', {
        name: this.name,
        server: this.server
      })
        .then(function (response) {
          console.log(response.data)
          self.summonerId = response.data.id
          axios.post('../php/leaguePos.php', {
            id: self.summonerId,
            server: self.server
          })
            .then(function (response) {
              console.log(response.data)
              for (var i = 0; i < response.data.length; i++) {
                if (response.data[i].queueType === 'RANKED_SOLO_5x5') {
                  self.lp = response.data[i].leaguePoints
                  self.rank = response.data[i].rank
                  self.tier = response.data[i].tier
                  self.wr = Math.round((response.data[i].wins / (response.data[i].wins + response.data[i].losses)) * 100)
                }
              }
            })
            .catch(function (e) {
              console.log(e)
            })
        })
        .catch(e => {
          console.log(e)
        })
    },
    getCurrentGame: function () {
      var self = this
      axios.post('../php/testing/php', {
        name: this.name,
        server: this.server
      })
      .then(function (response) {
        self.summonerId = response.data.id
        axios.post('../php/current.php', {
          id: self.summonerId,
          server: self.server
        })
          .then(function (response) {
            console.log(response.data)
            for (var i = 0; i < response.participants.length; i++) {
              self.gameIds.push(response.participants[i].summonerId)
            }
          })
          .catch(function (e) {
            console.log(e)
          })
      })
      .catch(function (e) {
        console.log(e)
      })
    }
  }
}
</script>



